@import "../_cms-variables";
@import "../_cms-mixins";

.cmp-search(){
	
	.inner{
		width: 100%;
		max-width: @container-max-width;
		margin: 0 auto;
		z-index: 200;
		transform: translateZ(0);

		&-active{
			.fixed(0,0,_,0);
		}
	}

	.search{
		@box-size: 36;

		padding: 7px 14px;
		background: #fff;

		.search-col{
			font-size: @font-size-xl;
			color: @ht-color;
			float: left;
			line-height: unit(@box-size,px);
		}

		.box{
			position: relative;
			height: unit(@box-size, px);
			background: #E5E5E5;
			border-radius: 4px;

			&.box-mar{
				margin-left: 30px;
			}

			input{
				border: none;
				height: unit(@box-size, px);
				background: #E5E5E5;
				border-radius: 4px;
				user-select: initial;
				display: block;
				outline: 0;
				padding: 0 36px;
				width: 75%;
				font-size: @font-size-base;
			}

			i{
				.absolute(0,_,_,_);
				line-height: unit(@box-size, px);
				color: #a8a8a8;
				width: unit(@box-size, px);
				text-align: center;
				font-size: @font-size-sm * 2;

				&.i-search{
					left: 0;
				}

				&.i-clear{
					right: 0;
					font-size: @font-size-lg;
					display: none;
				}
			}
		}


	}
}