@import "../_brand-variables.less";
@import "../_brand-mixins.less";


.fn-@{cms-ns}-columns{
		height: 100%;
		width: 100%;
		max-width: @container-max-width ;
		padding-top: 1.92rem;
		position: absolute;
		top: 0;
		box-sizing: border-box;

	.side-box{
		height: 100%;
		overflow-x: hidden;
		overflow-y: auto;
		float: left;
		width: 170%/7.5;

		.side-title{
			color: #666;
			padding: 45/2px 0;
			padding-left: 10px;
			display: block;
			line-height: 0;
			background: #f4f4f4;
			border-bottom: 1px solid #fff;
		}

		.side-active{
			background: #fff;
			color: @sidebar-color;
			position: relative;

			&:before{
				.size(4px,100%);
				.absolute(0,_,_,0);
				content: "";
				display: block;
				background: @sidebar-color;
			}
		}
	}

	.side-content{
		height: 100%;
		overflow-x: hidden;
		overflow-y: auto;
		background: #fff;

		.main-nav{
			a{
				color: #999;
				font-size: @font-size-lg;
				line-height: 0;
				padding: 33/2*@rex 0;
				margin:0px 18px;
				border-bottom: 2px solid transparent;
			}

			.active{
				color: @price-color;
				border-color: @price-color;
			}
		}

		.main-menu{

			.main-box{
				border-bottom: 1px solid #f4f4f4;
			}

			.main-title{
				display: block;
				font-size: @font-size-base;
				font-weight: bold;
				margin: 20px 0px;
				padding-left: 5px;
				line-height: 1;
				border-left: 2px solid @price-color;

				span{
					float: right;
					color: #666;
				}
			}

			.gutter-row{
				padding: 0 12px;
				text-align: center;

				span{
					.ellipsis();
					font-size: @font-size-sm;
					font-weight: bold;
					line-height: 29*@rex;
				}


			}
		}
	}
}
